//afterEach
router.afterEach((to, from) => {
    if (to.path === "/login") {
        window.localStorage.clear();
        window.sessionStorage.clear();

        console.log("");
    }

    if (to.path === "/homePage") {
        store.dispatch('pullbuttomselected', "homePage")
    }

    if (to.path === "/news") {
        store.dispatch('pullbuttomselected', "news")
    }

    if (to.path === "/community") {
        store.dispatch('pullbuttomselected', "community")
    }

    if (to.path === "/find") {
        store.dispatch('pullbuttomselected', "find")
    }

    if (to.path === "/news") {
        store.dispatch('pullbuttomselected', "news")
    }

    if (to.path === "/me") {
        store.dispatch('pullbuttomselected', "me")
    }

})

//
router.beforeEach((to, from, next) => {
    //
    let isLogin = window.localStorage.getItem("isLogin");

// alert(isLogin)
    /********************* */
    if (to.meta.needbuttomnav) {
        store.dispatch('pullbottomnavstate', true)
    } else {
        if (!store.state.bottomnavstate == false) {
            store.dispatch('pullbottomnavstate', false)
        }
    }

    if (to.meta.dontneedLogin) {
        next();
    } else {
        if (isLogin == true || isLogin == "true") {
            next();
        } else {
            window.localStorage.clear();
            window.sessionStorage.clear();
            next({
                path: '/login',
                query: { redirect: to.fullPath }
            })
        }
    }
})

export default router;


